import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

//全局加载动画
const radioStore = {
    state: {
        isShow: false
    },
    mutations: {
        showLoading(state, payLoad) {
            state.isShow = true;
        },
        closeLoading(state, payLoad) {
            state.isShow = false;
        },
    }
};

let store = new Vuex.Store({
    modules: {
        radioStore,
    }

});

export default store;


































