import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false;
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);

import WarWolfUI from 'war-wolf-ui'
import 'war-wolf-ui/lib/war-wolf-ui.css'
import 'war-wolf-ui/lib/iconfont.css'

Vue.use(WarWolfUI);
import store from './store'

//引入全局样式
import './assets/css/reset.css'
import '../static/style/war-wolf-ui.css'
import './assets/css/global.css'
//引入iconfont
//import './assets/css/iconfont.css'
//引入animate动画
import '../static/style/animate.css'

//代码高亮
import hljs from 'highlight.js'
import '../static/style/tomorrow-night-eighties.css'
// 增加自定义命令v-highlight
Vue.directive("highlight", function (el) {
    let blocks = el.querySelectorAll("pre code");
    blocks.forEach(block => {
        hljs.highlightBlock(block);
    });
});
// 增加组定义属性，用于在代码中预处理代码格式
Vue.prototype.$hljs = hljs;

new Vue({
    el: '#app',
    store,
    router,
    components: {App},
    template: '<App/>'
})



