import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    mode: 'hash',
    routes: [
        {
            path: '/',
            redirect: '/index'
        },
        {
            path: '*',
            name: '/404',
            component: resolve=>(require(["@/components/404.vue"],resolve)),
        },
        {
            path: '/home',
            name: 'home',
            component: resolve=>(require(["@/components/Master.vue"],resolve)),
            redirect:'/index',
            children:[
                {
                    path: '/index',
                    name: 'index',
                    component: resolve=>(require(["@/components/deveGuide/Index.vue"],resolve))
                },
                {
                    path: '/quickStart',
                    name: 'quickStart',
                    component: resolve=>(require(["@/components/deveGuide/QuickStart.vue"],resolve))
                },
                {
                    path: '/codeStandard',
                    name: 'codeStandard',
                    component: resolve=>(require(["@/components/deveGuide/CodeStandard.vue"],resolve))
                },
                {
                    path: '/icon',
                    name: 'icon',
                    component: resolve=>(require(["@/components/base/Icon.vue"],resolve))
                },
                {
                    path: '/button',
                    name: 'button',
                    component: resolve=>(require(["@/components/base/Button.vue"],resolve))
                },
                {
                    path: '/commonCode',
                    name: 'commonCode',
                    component: resolve=>(require(["@/components/others/CommonCode.vue"],resolve))
                },
                {
                    path: '/txtLinkHref',
                    name: 'txtLinkHref',
                    component: resolve=>(require(["@/components/base/TxtLinkHref.vue"],resolve))
                },
                {
                    path: '/radio',
                    name: 'radio',
                    component: resolve=>(require(["@/components/form/Radio.vue"],resolve))
                },
                {
                    path: '/checkbox',
                    name: 'checkbox',
                    component: resolve=>(require(["@/components/form/Checkbox.vue"],resolve))
                },
                {
                    path: '/input',
                    name: 'input',
                    component: resolve=>(require(["@/components/base/Input.vue"],resolve))
                },
                {
                    path: '/countNumber',
                    name: 'countNumber',
                    component: resolve=>(require(["@/components/base/CountNumber.vue"],resolve))
                },
                {
                    path: '/table',
                    name: 'table',
                    component: resolve=>(require(["@/components/form/Table.vue"],resolve))
                },
                {
                    path: '/badge',
                    name: 'badge',
                    component: resolve=>(require(["@/components/base/Badge.vue"],resolve))
                },
                {
                    path: '/breadcrumb',
                    name: 'breadcrumb',
                    component: resolve=>(require(["@/components/base/Breadcrumb.vue"],resolve))
                },
                {
                    path: '/addressList',
                    name: 'addressList',
                    component: resolve=>(require(["@/components/business/AddressList.vue"],resolve))
                },
                {
                    path: '/addressEdit',
                    name: 'addressEdit',
                    component: resolve=>(require(["@/components/business/AddressEdit.vue"],resolve))
                },
                {
                    path: '/goodsCard',
                    name: 'goodsCard',
                    component: resolve=>(require(["@/components/business/GoodsCard.vue"],resolve))
                },
                {
                    path: '/coupon',
                    name: 'coupon',
                    component: resolve=>(require(["@/components/business/Coupon.vue"],resolve))
                },
                {
                    path: '/payOrRecharge',
                    name: 'payOrRecharge',
                    component: resolve=>(require(["@/components/business/PayOrRecharge.vue"],resolve))
                },
                {
                    path: '/addressSelect',
                    name: 'addressSelect',
                    component: resolve=>(require(["@/components/business/AddressSelect.vue"],resolve))
                },
                {
                    path: '/firstFrame',
                    name: 'firstFrame',
                    component: resolve=>(require(["@/components/business/FirstFrame.vue"],resolve))
                },
                /*{
                    path: '/tim',
                    name: 'tim',
                    component: resolve=>(require(["@/components/business/Tim.vue"],resolve))
                },*/
                {
                    path: '/obs',
                    name: 'obs',
                    component: resolve=>(require(["@/components/business/Obs.vue"],resolve))
                },
                {
                    path: '/kdzf',
                    name: 'kdzf',
                    component: resolve=>(require(["@/components/business/Kdzf.vue"],resolve))
                },
                {
                    path: '/kdShare',
                    name: 'kdShare',
                    component: resolve=>(require(["@/components/business/KdShare.vue"],resolve))
                },
            ]
        },

    ]
})



